<template>
  <div id="actor">

  <div id="slideshow">
    <el-carousel :interval="4000" type="card" height="480px">
      <el-carousel-item v-for="(item,key) in list" :key="key">
        <img v-bind:src="item"/>
        <h3>{{item}}</h3>
      </el-carousel-item>
    </el-carousel>
  </div>

  </div>
</template>
<script>
  export default {
    data(){
      return{
        list:['./static/assets/liangnaduo/20180614135036.png','./static/assets/thumb_4.jpg','./static/assets/thumb_2.jpg','./static/assets/thumb_3.jpg','./static/assets/thumb_5.jpg']
      }
    }
  }
</script>
<style>
  #actor{
    display:flex;
    width:100%;
    margin:0 auto;
  }
  #slideshow {
    flex:1;
 margin-top:10px;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>



