<template>
  <div id="header">
    <div id="search">


    <ul >
      <li class="li1">豆瓣电影</li>
      <li class="li2"><input type="text" placeholder="搜索电影、电视剧、综艺、影人"> <el-button type="primary" size="small" icon="el-icon-search">搜索</el-button></li>
    </ul>
    </div>
      <!--导航栏-->
    <div id="nav">
      <!--导航栏左边-->


      <el-menu

        id="left"
        :default-active="activeIndex2"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b">
        <el-menu-item index="1"><router-link to="/">首页</router-link></el-menu-item>
        <el-menu-item index="2" ><router-link to="/worldcup">世界杯</router-link></el-menu-item>
        <el-menu-item index="3" ><router-link to="/movie">电影中心</router-link></el-menu-item>
        <el-menu-item index="4"><router-link to="/actor">演员中心</router-link></el-menu-item>
      </el-menu>

      <!--导航栏右边-->

      <el-menu
        id="right"
        :default-active="activeIndex2"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b">

        <el-menu-item index="5" ><router-link to="/movie"><span class="el-icon-mobile-phone"></span><span >移动端</span ></router-link></el-menu-item>
        <el-menu-item index="6" ><router-link to="/actor"><span class="el-icon-time"></span><span >观看记录</span ></router-link></el-menu-item>
        <el-menu-item index="7" v-if="!session.login=='1'" ><router-link to="/login" class="right"><i class="el-icon-time"></i><span >登陆</span ></router-link></el-menu-item>
        <el-menu-item index="9" v-if="!session.login=='1'" ><router-link to="/regist" class="right"><span >注册</span ></router-link></el-menu-item>
        <el-menu-item index="7" v-if="session.login=='1'" ><router-link to="/login"><i class="el-icon-info"></i><span >欢迎你，{{session.username}}</span ></router-link></el-menu-item>
        <el-menu-item index="8" v-if="session.login=='1'" @click="doLogout"><i class="el-icon-circle-close-outline"></i><span>注销</span ></span></el-menu-item>
      </el-menu>


    </div>


  </div>
</template>
<script>
  export default {
    data() {
      return {
        activeIndex: '1',
        activeIndex2: '1',
        //将sessionStorage.login的值赋给session.login,利用v-if="session=='1'"进行条件渲染,当login为1时渲染欢迎你，非1时显示登陆
        session:{"login":sessionStorage.login,"username":sessionStorage.username}
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      doLogout(){
        sessionStorage.login='';
        window.location='/';
      }
    }
  }
</script>
<style>
  #header{
    display:flex;
    /*flex: 1;*/
    flex-direction:column;
    background-color: #b0ced1;
  }
  /*头部搜索框的样式*/
  .li1,.li2{
    height: 45px;
    display:inline-block;
    padding: 10px;
    vertical-align:middle;
    font-size:30px;
  }

  .li2 input{
    height:30px;
    outline: none;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border:none;
    display:inline-block;

  }
  #header a{
    width:100px;
    height:60px;
    text-decoration:none;
    display:inline-block;
   }


#nav{
/*width:1380px;*/
  display:flex;
  justify-content: space-between;
  /*margin:0 auto;*/
  /*padding: 0 auto;*/
  background-color: rgb(84, 92, 100);
  /*让边框合并*/
  border-collapse:collapse
  }
#left,#right{
  width:50%;
  flex:auto;
}
#nav ul{
  display:flex;
}
/*让导航栏的右半部分的内容靠近右边框*/
#right{
  display:flex;
  justify-content: flex-end;
}



  /*#ul2{*/
/*display:inline-block;*/
    /*position:absolute;*/
    /*top:0;*/
    /*right:0;*/
  /*}*/

  /*#left,#right{*/

    /*display:inline-block;*/

  /*}*/
  /*#left{*/
    /*float:left;*/
  /*}*/
  /*#right{*/
    /*float:right;*/
  /*}*/
  /*清除导航栏浮动，需要在浮动的盒子的父盒子设置*/
  /*#nav:after{*/
    /*content:"";clear:both;display:block*/
  /*}*/
  #actor
.el-header, .el-footer {
background-color: #B3C0D1;
color: #333;
text-align: center;
line-height: 60px;
}

.el-aside {
background-color: #D3DCE6;
color: #333;
text-align: center;
line-height: 200px;
}

.el-main {
background-color: #E9EEF3;
color: #333;
text-align: center;
line-height: 160px;
}

body > .el-container {
margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
line-height: 320px;
}

</style>



