<template>
  <div id="worldcup">
    <div id="news">
  <div id="hotnews">
    <h2>热门资讯</h2>
    <router-link :to="'/newscontent/'+item.id" v-for="(item,key) in list"><div class="grid-content bg-purple" >{{item.title}}</div></router-link></el-col>

  </div>
    <div id="newnews">
      <h2>最新资讯</h2>
      <router-link :to="'/newscontent/'+item.id" v-for="(item,key) in list"><div class="grid-content bg-purple" >{{item.title}}</div></router-link></el-col>

    </div>
    <div id="cridictnews">
      <h2>深度评论</h2>
      <router-link :to="'/newscontent/'+item.id" v-for="(item,key) in list"><div class="grid-content bg-purple" >{{item.title}}</div></router-link></el-col>

    </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
  return {
    list:[]
  }
  },
  methods:{
    requestData(){
      this.$http.get('http://localhost:3000/news').then((res)=>{
        console.log(res);
        this.list=res.body.news;
      },(err)=>{
        console.log(err);
      })
    }
  },
  mounted(){
    this.requestData();
  }
}
</script>
<style>
  #news{
display:flex;
   justify-content:center;
  }
  #news div{
    flex:1;
  }
</style>



