<template>
  <div id="login">
    <div id="regist">
      <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm" >
        <el-form-item label="账号" prop="username">
          <el-input type="text" v-model="ruleForm2.username" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPass">
          <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input v-model.number="ruleForm2.age"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm">提交</el-button>
          <el-button @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
      <div id="alert">
        <el-alert v-if="regist=='1'"
          title="恭喜你，注册成功"
          type="success"
          description="豆瓣电影欢迎你的加入噢"
          show-icon>
        </el-alert>
        <el-alert v-if="regist=='2'"
          title="用户名已被注册"
          type="info"
          description="这个这个用户名太抢手了噢，已经被其他用户注册啦，试试其他的吧"
          show-icon>
        </el-alert>
        <!--<el-alert-->
          <!--title="警告提示的文案"-->
          <!--type="warning"-->
          <!--description="文字说明文字说明文字说明文字说明文字说明文字说明"-->
          <!--show-icon>-->
        <!--</el-alert>-->
        <!--<el-alert-->
          <!--title="错误提示的文案"-->
          <!--type="error"-->
          <!--description="文字说明文字说明文字说明文字说明文字说明文字说明"-->
          <!--show-icon>-->
        <!--</el-alert>-->
      </div>
    </div>

  </div>
</template>
<script>
  export default {
    data() {
      //验证年龄
      var checkAge = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('年龄不能为空'));
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error('请输入数字值'));
          } else {
            if (value < 18) {
              callback(new Error('必须年满18岁'));
            } else {
              callback();
            }
          }
        }, 1000);
      };
      // 验证密码
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('checkPass');
          }
          callback();
        }
      };
      // 验证再次输入的密码是否正确
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      //验证账号
      var validateUsername=(rule,value,callback)=>{
        if(value===''){
          callback(new Error('账号不能为空'));
        }else if(value.length>16){
          callback(new Error('账号长度不能超过16位'))
        }else{
          callback();
        }
      }
      return {
        ruleForm2: {
          username:'',
          pass: '',
          checkPass: '',
          age: '',
        },
        rules2: {
          username:[
            { validator: validateUsername,trigger:'blur'}
            ],
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          age: [
            { validator: checkAge, trigger: 'blur' }
          ]
        },
        regist:''
      };
    },
    methods: {
      submitForm() {
        this.$refs.ruleForm2.validate((valid) => {
          if (valid) {
           this.$http.post('http://localhost:3000/regist',{'username':this.ruleForm2.username,'pass':this.ruleForm2.pass,'age':this.ruleForm2.age}).then((res)=>{
             alert('提交成功');
             console.log(res.body.login);
             this.regist=res.body.regist;
             //alert窗口出现3秒后清除弹窗
             setTimeout(this.claesrAlert,10000);
             localStorage.login=res.body.login;
             localStorage.username=res.body.username;
           },(res)=>{
             console.log('提交失败');
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      //清除注册成功或者报错的弹窗
      claesrAlert(){
        this.regist='';
      }
    }
  }
</script>

<style>
  #regist{
    width:1380px;
    height:768px;
    background-image: url("../assets/background.jpg");
    background-size: 1380px 810px;
    margin:0 auto;
    position:relative;
  }
  #regist form{
    width:400px;
    height:400px;
    position:absolute;
    top:200px;
    right:150px;
    background-color: rgba(112,112,112,0.7);
  }
  #regist label{
    color:white;
  }
  #regist input{
    display:inline-block;
    height:30px;
    margin-top:20px;
    border-radius: 10px;
    outline: none;
    border:none;
  }
  #regist button{
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 10px 15px;
    font-size: 16px;
    border-radius: 10px;
    outline:none;
    display:inline-block;
  }
  .button1{
    margin:20px 0 0 75px;
  }
   .button2{

    margin:20px 0 0 30px;
  }
  #regist a,p{
    color:white;
    text-decoration:none;
  }
  /*浏览器的审查元素功能，可以知道警告栏的class，从而设置它的颜色*/
  .el-form-item__error{
    color:yellow;
  }
  #alert{
    width:300px;
    height:100px;
    position:absolute;;
    top:600px;
    right:190px;
  }
</style>



