import Vue from 'vue'
import App from './App.vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import router from './router/router'
import VueResource from 'vue-resource'
Vue.use(ElementUI);
Vue.use(VueResource);
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



