<template>
  <div>
    {{newscontent}}
  </div>
</template>
<script>
  export default {
    data(){
      return {
        newscontent:''
      }
    },
    methods:{
      requestData() {
        var url ='http://localhost:3000/news';
        this.$http.get(url).then((res)=>{
          console.log(this.$route.params.id);
          //因为id是从零开,所以当用id做数组的索引的时候要在id的基础上减1
          this.newscontent=res.body.news[this.$route.params.id-1].content;
        },(err)=>{
          console.log(err);
        })
      }
    },
    mounted(){
      this.requestData();
    }
  }
</script>



