<template>
  <div id="login">
    <div id="regist">
      <!--当处于登陆状态的时候，登陆栏消失-->
    <form v-if="session.login=='1'">
      <label for="username">用户名：  <input id='username' type="text" name="username" ref="username"></label><br>

      <label for="pass">密&nbsp&nbsp&nbsp&nbsp码：  <input id='pass' type="password" name="pass" ref="pass"></label><br>
        <button  @click="doLogin">登录</button><br><br>
      <p><router-link to="/regist">没有账号？轻松注册</router-link></p>
    </form>




    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return{
        // 取反，这样当login=='1'成立的时候登陆栏消失，当login不为"1"，也就是未登录时，登陆栏出现
        // 取反，这样当login=='1'成立的时候登陆栏消失，当login不为"1"，也就是未登录时，登陆栏出现
      session:{"login":!sessionStorage.login}
      }
    },
    methods:{
      doLogin(){
        var username=this.$refs.username.value;
        var pass=this.$refs.pass.value;
        alert(username+"..."+pass);
        this.$http.post('apis/login',{"username":username,"pass":pass}).then(
        (res)=>{
          alert(res.data.data);
          if(res.data.login=='1'){
            //sessionStorage 方法针对一个 session 进行数据存储。当用户关闭浏览器窗口后，数据会被删除.sessionStorage是window的一个属性，window.sessionStorage,通常window省略
            // alert(res.login);
            //将sessionStorage.login设置为1，保存登陆状态

            sessionStorage.login=res.data.login;
            sessionStorage.username=res.data.username;
          }else{
            sessionStorage.login='';
          }
          //自动跳转首页
          // window.location='/';
        },(res)=>{
          alert('登陆失败');
        }
      )
      }
    }
    }
</script>

<style>
#regist{
  width:1380px;
  height:768px;
  background-image: url("../assets/background.jpg");
  background-size: 1380px 810px;
  margin:0 auto;
  position:relative;
}
  #regist form{
    width:350px;
    height:300px;
    position:absolute;
    top:200px;
    right:150px;
    background-color: rgba(112,112,112,0.7);

  }
  #regist label{
    color:white;
  }
  #regist input{
    display:inline-block;
    height:30px;
    margin-top:20px;
    border-radius: 10px;
    outline: none;
    border:none;
  }
  #regist button{
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 10px 25px;
    font-size: 16px;
    border-radius: 10px;
    outline:none;
    margin:20px 0 0 70px;
  }
  #regist a{
    color:white;
    text-decoration:none;
  }
  #regist a:hover{
    color: yellow;
  }
</style>



